import React, { Fragment } from 'react';
import { useDispatch } from 'react-redux';
import { Button, Typography, TextField } from '@mui/material';
import { addAlert, removeAlerts } from 'src/store/slices/alert';
import { useSnackbar } from 'notistack';

const Test = () => {
  const dispatch = useDispatch();
  const { closeSnackbar } = useSnackbar();

  const handleClick = () => {
    dispatch(
      addAlert({
        message: 'Failed fetching data.',
        options: {
          key: new Date().getTime() + Math.random(),
          variant: 'warning',
          action: (key) => <Button onClick={() => closeSnackbar(key)}>dismiss me</Button>
        }
      })
    );
  };

  const handleDimissAll = () => {
    dispatch(removeAlerts());
  };

  return (
    <Fragment>
      <Typography variant="h4" gutterBottom>
        Notistack redux test
      </Typography>
      <Button variant="outlined" onClick={handleClick}>
        Display snackbar
      </Button>
      <Button variant="outlined" onClick={handleDimissAll}>
        Dismiss all snackbars
      </Button>
      <TextField placeholder="User Name" />
    </Fragment>
  );
};

export default Test;
